// react-bootstrap  npm install
//   (Button, Navbar, Nav ...) import 
// import { useState } from "react";
import { Route, Routes, Link, useNavigate, Outlet } from "react-router-dom";
import { Navbar, Container, Nav, NavDropdown } from "react-bootstrap";
import { createContext, useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";

import logo from "./logo.svg";

// js .js   
// import { a, b } from "./data";

import Detail from "./routes/Detail";
import Main from "./routes/Main";
import About from "./routes/About";
import Event from "./routes/Event";
import Cart from "./routes/Cart";

import "./App.css";

//  

import data from "./data/data";

let _PUBLIC_URL = process.env.PUBLIC_URL;

// state  context 
export let Context1 = createContext();

function App() {
    //    
    // detail.js 
    // useEffect(() => {
    //     if (!localStorage.getItem("recent")) {
    //         localStorage.setItem("recent", JSON.stringify([]));
    //     }
    // });

    //   
    // <Link to={}> </Link>  onClick={() => navigate("path")}    
    let navigate = useNavigate();

    let [stock, setStock] = useState(321);
    let [shoes, setShoes] = useState(data);
    let [loading, setLoading] = useState(false);

    // localstorage  + json
    let obj = { name: "kim" };
    //  
    localStorage.setItem("data", JSON.stringify(obj));
    // text 
    let text = localStorage.getItem("data");
    // console.log(text);
    // console.log(JSON.parse(text));

    let state = useSelector((state) => {
        return state;
    });
    let dispatch = useDispatch();

    return (
        <>
            {loading && <Loading />}

            <div className="App">
                <Navbar bg="dark" variant="dark" sticky="top">
                    <Container>
                        <Navbar.Brand>
                            <Link to="/"> DEMO</Link>
                        </Navbar.Brand>
                        <Nav className="me-auto">
                            <Nav.Link onClick={() => navigate("/")}></Nav.Link>
                            <NavDropdown title="" id="nav-dropdown">
                                <NavDropdown.Item eventKey="4.1" onClick={() => navigate("/detail/0")}>
                                    0
                                </NavDropdown.Item>
                                <NavDropdown.Item eventKey="4.2" onClick={() => navigate("/detail/1")}>
                                    1
                                </NavDropdown.Item>
                                <NavDropdown.Divider />
                                <NavDropdown.Item eventKey="4.3" onClick={() => navigate("/detail/2")}>
                                    2
                                </NavDropdown.Item>
                                <NavDropdown.Item eventKey="4.4" onClick={() => navigate("/detail/3")}>
                                    3
                                </NavDropdown.Item>
                            </NavDropdown>
                            {/* <Nav.Link onClick={() => navigate("/detail/0")}></Nav.Link> */}

                            <Nav.Link onClick={() => navigate("/about")}></Nav.Link>
                            <Nav.Link onClick={() => navigate("/event")}></Nav.Link>
                            <Nav.Link onClick={() => navigate("/cart")}></Nav.Link>

                            {/*      */}
                            <Nav.Link onClick={() => navigate(1)}></Nav.Link>
                            <Nav.Link onClick={() => navigate(-1)}></Nav.Link>
                        </Nav>
                    </Container>
                </Navbar>

                <div className="abc"> app </div>
                <Routes>
                    <Route exact path="/" element={<Main shoes={shoes} setShoes={setShoes} data={data} setLoading={setLoading} />} />

                    {/* <Route path="/detail" element={<Detail shoes={shoes} />}></Route> */}
                    {/*   . url parameter :id  */}
                    <Route
                        path="/detail/:id"
                        element={
                            // state  context 
                            <Context1.Provider value={{ stock, shoes }}>
                                <Detail shoes={shoes} />
                            </Context1.Provider>
                        }
                    ></Route>

                    <Route path="/about" element={<About />}>
                        {/* Nested Routes */}
                        {/*     */}
                        {/* /about/member */}
                        <Route path="member" element={<div></div>} />
                        {/* /about/location */}
                        <Route path="location" element={<div></div>} />
                    </Route>
                    <Route path="/event" element={<Event />}>
                        <Route path="one" element={<div>   </div>} />
                        <Route path="two" element={<div> </div>} />
                    </Route>
                    <Route path="/cart" element={<Cart />}></Route>

                    {/*   (404 ) */}
                    <Route path="*" element={<div> . 404 </div>} />
                </Routes>

                <Recent recent={state.recent} />
                <h3></h3>
                {state.recent}
            </div>
        </>
    );
}

function Loading() {
    return (
        <div className="loading">
            <div className="loading-wrap">
                <div className="loading-wheel"></div>
            </div>
        </div>
    );
}

function Recent(props) {
    let recent = props.recent;
    console.log(recent);

    return (
        <div>
            <h4>  </h4>
            <div>
                {recent.map((o) => {
                    let fData = data.find((p) => p.id == o);

                    if (fData) {
                        return (
                            <div key={o}>
                                <Link to={`/detail/${fData.id}`}>{fData.title}</Link>
                            </div>
                        );
                    } else {
                        return <div>  </div>;
                    }
                })}
            </div>
        </div>
    );
}

export default App;
