import { useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Nav, Link } from "react-bootstrap";
import styled, { css } from "styled-components";
import { Context1 } from "./../App";
import { useSelector, useDispatch } from "react-redux/es/exports";
import { addCart, changeRecent } from "../store";

let _PUBLIC_URL = process.env.PUBLIC_URL;

let DfBtn = styled.button`
    padding: 10px;
    background: #ffff00;
    color: #000;
`;

let DefaultBox = styled.div`
    padding: 30px;
    border: 1px solid #000;

    //  
    button {
        border: 3px solid;

        &.pp {
            font-size: 2rem;
        }
    }
`;

//   
let DdBtn = styled(DfBtn)`
    // props , if  
    color: ${(props) => props.color || "blue"};

    ${(props) =>
        props.big &&
        css`
            width: 100px;
            height: 60px;
        `}
`;

function Detail(props) {
    //   . object  
    // let a = useContext(Context1);
    // console.log(a);
    let { stock, shoes } = useContext(Context1);

    let [ttestt, setTtestt] = useState(true);
    let [iValue, setIValue] = useState("");
    let [tab, setTab] = useState(0);

    let state = useSelector((state) => {
        return state;
    });
    let dispatch = useDispatch();

    useEffect(() => {
        // mount, update   (  ) .
        // html   .
        //  ,   ,     

        if (ttestt) {
            setTimeout(() => {
                let box = document.getElementsByClassName("ttestt")[0];
                if (box) {
                    box.style.display = "none";
                    setTtestt(false);
                    console.log("use Effect ");
                }
            }, 1000);
        }

        // clean up.  return useEffect   .
        return () => {
            //   
            //    .
            //  mount     unmount  
            console.log("  ");
        };
    }, [ttestt]);
    // dependency
    // []  state   state   . []   

    useEffect(() => {
        let a = /\D/g;

        if (a.test(iValue)) {
            alert(" .");
            setIValue(iValue.replace(a, ""));
        }
    }, [iValue]);

    useEffect(() => {
        if (shoe) {
            dispatch(changeRecent(shoe.id));
        }

        // dispatch();
    });

    // :id   
    let { id } = useParams(),
        // arr[id]  ,   state    
        shoe = props.shoes.find((o) => o.id == id);

    if (shoe) {
        return (
            <>
                {stock}
                <div>{iValue}</div>
                <input type="text" placeholder=" ." onChange={changeIValue} value={iValue} />
                <br />
                <button onClick={() => setTtestt(!ttestt)}> {ttestt ? "" : ""}</button>
                {ttestt ? (
                    <div className="ttestt" style={{ background: "yellow" }}>
                         or  
                    </div>
                ) : null}

                <DefaultBox>
                    <DfBtn>123</DfBtn>
                    <DdBtn className="pp" color="#f0f">
                        1234
                    </DdBtn>
                    <DdBtn big>1234</DdBtn>
                </DefaultBox>

                <div className="container">
                    <div className="row">
                        <div className="col-md-6">
                            <img src={_PUBLIC_URL + "/img/banner/sub_banner" + shoe.id + ".jpg"} alt="" style={{ width: "100%" }} />
                        </div>
                        <div className="col-md-6">
                            <h4 className="pt-5">
                                {shoe.title} {id}
                            </h4>
                            <p>{shoe.content}</p>
                            <p>{shoe.price}</p>
                            <button className="btn btn-danger" onClick={() => dispatch(addCart(shoe))}>
                                
                            </button>
                        </div>
                    </div>
                </div>

                <Nav variant="pills" defaultActiveKey="link0">
                    <Nav.Item>
                        <Nav.Link onClick={() => setTab(0)} eventKey="link0">
                            Active
                        </Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link onClick={() => setTab(1)} eventKey="link1">
                            Option 2
                        </Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link onClick={() => setTab(2)} eventKey="link2">
                            Option 3
                        </Nav.Link>
                    </Nav.Item>
                </Nav>

                {/* if      */}
                <TabInner tab={tab} />
            </>
        );
    } else {
        return (
            <>
                <div>{id}    .</div>
            </>
        );
    }

    function changeIValue(e) {
        setIValue(e.target.value);
    }
}

// props    parameter {}  props   
function TabInner({ tab }) {
    let [fade, setFade] = useState("");

    //    context  
    let { stock } = useContext(Context1);

    useEffect(() => {
        // automatic batching .
        //  state     
        setTimeout(() => {
            setFade("end");
        }, 100);

        return () => {
            setFade("");
        };
    }, [tab]);

    // let { tab } = props;

    // switch (tab) {
    //     case 0:
    //         return <div>0</div>;
    //     case 1:
    //         return <div>1</div>;
    //     case 2:
    //         return <div>2</div>;
    // }

    // array    
    //  ...
    return (
        <div className={`start ${fade}`}>
            {stock}
            {[<div>0</div>, <div>1</div>, <div>2</div>][tab]}
        </div>
    );
}

export default Detail;
