import { Link, Outlet } from "react-router-dom";

function Event() {
    return (
        <>
            <Link to="/event"></Link>
            <Link to="one">1</Link>
            <Link to="two">2</Link>

            <h4> </h4>
            <Outlet></Outlet>

            {
                {
                    a: <div>a</div>,
                    b: <div>b</div>,
                    c: <div>c</div>,
                    d: <div>d</div>
                }["c"]
            }
        </>
    );
}

export default Event;
